#root > section.Hello {
  padding: 150px 0px;
  text-align: center;
  max-width: 600px;
  margin: 0px auto 100px;
}
#root > section.Hello > h3 {
  color: rgb(100, 255, 218);
  font-size: 17px;
  font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono",
    "Lucida Console", Monaco, monospace;
  font-weight: normal;
  -webkit-box-pack: center;
  justify-content: center;
}
#root > section.Hello > h3 > a {
  margin-right: 10px;
  font-family: monospace;
  font-weight: normal;
  color: rgb(100, 255, 218);
  font-size: 20px;
  position: relative;
}

#root > section.Hello > h4 {
  font-weight: 600;
  color: rgb(204, 214, 246);
  font-size: 60px;
  margin: 0px;
}
#root > section.Hello > div > p {
  color: rgb(136, 146, 176);
  line-height: 1.3;
  font-family: Calibre, "San Francisco", "SF Pro Text", -apple-system, system-ui,
    BlinkMacSystemFont, Roboto, "Helvetica Neue", "Segoe UI", Arial, sans-serif;
  font-size: 18px;
}
#root > section.Hello > a {
  display: inline-block;
  text-decoration: none;
  text-decoration-skip-ink: auto;
  position: relative;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
  cursor: pointer;
  color: rgb(100, 255, 218);
  background-color: transparent;
  border: 1px solid rgb(100, 255, 218);
  border-radius: 3px;
  padding: 1.25rem 1.75rem;
  font-size: 14px;
  font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono",
    "Lucida Console", Monaco, monospace;
  line-height: 1;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
  margin-top: 50px;
}
#root > section.Hello > a:hover {
  background-color: rgb(100 255 218 / 15%);
}
